.allDivDUF{
    height: 100%;
    width: 100%;
    border: 1px grey solid;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.inputDUForm{
    display: flex;
    border-radius: 10px;
    max-width: 600px;
    width: 100%;
    height: 40px;
    border: 1px grey solid;
    padding: 0 10px;
    margin: 20px 0;
}
    
.inputDUForm::placeholder {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
}